"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "@/contexts/AuthContext"
import { Permission } from "@/types/permissions"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { AlertCircle, Loader2, UserPlus, Edit, Trash, Shield, RefreshCw } from "lucide-react"
import { BackButton } from "@/components/ui/BackButton"
import { useToast } from "@/components/ui/use-toast"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import { Usuario } from "@/services/usuarioService"

export default function UsuariosPage() {
  const router = useRouter()
  const { hasPermission } = useAuth()
  const { toast } = useToast()
  const [usuarios, setUsuarios] = useState<Usuario[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [usuarioParaExcluir, setUsuarioParaExcluir] = useState<Usuario | null>(null)
  const [excluindo, setExcluindo] = useState(false)

  // Verificar se o usuário tem permissão para visualizar usuários
  const canViewUsers = hasPermission(Permission.VIEW_USERS) || hasPermission("USUARIOS_VISUALIZAR")
  const canCreateUser = hasPermission(Permission.CREATE_USER) || hasPermission("USUARIOS_CRIAR")
  const canEditUser = hasPermission(Permission.EDIT_USER) || hasPermission("USUARIOS_EDITAR")
  const canDeleteUser = hasPermission(Permission.DELETE_USER) || hasPermission("USUARIOS_REMOVER")
  const canManagePermissions = hasPermission(Permission.MANAGE_PERMISSIONS) || hasPermission("USUARIOS_PERMISSOES")

  // Carregar usuários
  const carregarUsuarios = async () => {
    if (!canViewUsers) {
      setError("Você não tem permissão para visualizar usuários.")
      setLoading(false)
      return
    }

    setLoading(true)
    setError(null)

    try {
      const data = await usuarioService.listarUsuarios()
      setUsuarios(data)
    } catch (err: any) {
      console.error("Erro ao carregar usuários:", err)
      setError("Erro ao carregar usuários. " + (err.message || "Tente novamente."))
    } finally {
      setLoading(false)
    }
  }

  // Carregar usuários ao montar o componente
  useEffect(() => {
    carregarUsuarios()
  }, [])

  // Função para confirmar exclusão de usuário
  const confirmarExclusao = (usuario: Usuario) => {
    setUsuarioParaExcluir(usuario)
  }

  // Função para cancelar exclusão
  const cancelarExclusao = () => {
    setUsuarioParaExcluir(null)
  }

  // Função para excluir usuário
  const excluirUsuario = async () => {
    if (!usuarioParaExcluir) return

    setExcluindo(true)

    try {
      await usuarioService.excluirUsuario(usuarioParaExcluir.id)

      toast({
        title: "Usuário excluído",
        description: `O usuário ${usuarioParaExcluir.nome} foi excluído com sucesso.`,
      })

      // Atualizar a lista de usuários
      setUsuarios((prev) => prev.filter((u) => u.id !== usuarioParaExcluir.id))

      // Fechar o diálogo
      setUsuarioParaExcluir(null)
    } catch (err: any) {
      console.error("Erro ao excluir usuário:", err)

      toast({
        title: "Erro ao excluir usuário",
        description: err.message || "Não foi possível excluir o usuário. Tente novamente.",
        variant: "destructive",
      })
    } finally {
      setExcluindo(false)
    }
  }

  // Se o usuário não tem permissão para visualizar usuários, mostrar mensagem
  if (!canViewUsers) {
    return (
      <div className="flex-1 space-y-4 p-4 md:p-8 pt-6">
        <div className="flex items-center gap-4 mb-4">
          <BackButton />
          <h2 className="text-3xl font-bold tracking-tight">Gerenciamento de Usuários</h2>
        </div>

        <div className="flex justify-center py-8">
          <Alert variant="destructive" className="max-w-md">
            <AlertCircle className="h-4 w-4" />
            <AlertDescription>Você não tem permissão para visualizar usuários.</AlertDescription>
          </Alert>
        </div>
      </div>
    )
  }

  const handleEditClick = (usuario: Usuario) => {
    router.push(`/usuarios/editar/${usuario.id}`)
  }

  const handleDeleteClick = (usuario: Usuario) => {
    confirmarExclusao(usuario)
  }

  return (
    <div className="flex-1 space-y-4 p-4 md:p-8 pt-6">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-4">
          <BackButton />
          <h2 className="text-3xl font-bold tracking-tight">Gerenciamento de Usuários</h2>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" onClick={carregarUsuarios} disabled={loading}>
            {loading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <RefreshCw className="mr-2 h-4 w-4" />}
            Atualizar
          </Button>
          {canCreateUser && (
            <Button onClick={() => router.push("/usuarios/novo")}>
              <UserPlus className="mr-2 h-4 w-4" />
              Novo Usuário
            </Button>
          )}
        </div>
      </div>

      {error && (
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <Card>
        <CardHeader>
          <CardTitle>Lista de Usuários</CardTitle>
        </CardHeader>
        <CardContent>
          {loading ? (
            <div className="flex justify-center py-8">
              <Loader2 className="h-8 w-8 animate-spin text-primary" />
              <p className="ml-2">Carregando usuários...</p>
            </div>
          ) : usuarios.length === 0 ? (
            <div className="text-center py-8 text-muted-foreground">Nenhum usuário encontrado.</div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Nome</TableHead>
                  <TableHead>Usuário</TableHead>
                  <TableHead>Perfil</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead className="text-right">Ações</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {usuarios.map((usuario) => (
                  <TableRow key={usuario.id}>
                    <TableCell className="font-medium">{usuario.nome}</TableCell>
                    <TableCell>{usuario.username}</TableCell>
                    <TableCell>
                      {usuario.perfil === "ADMIN" ? (
                        <Badge variant="default">Administrador</Badge>
                      ) : (
                        <Badge variant="outline">Operador</Badge>
                      )}
                    </TableCell>
                    <TableCell>
                      {usuario.ativo ? (
                        <Badge variant="success" className="bg-green-100 text-green-800 hover:bg-green-200">
                          Ativo
                        </Badge>
                      ) : (
                        <Badge variant="destructive" className="bg-red-100 text-red-800 hover:bg-red-200">
                          Inativo
                        </Badge>
                      )}
                    </TableCell>
                    <TableCell className="text-right">
                      <div className="flex justify-end gap-2">
                        <Button size="sm" variant="ghost" onClick={() => handleEditClick(usuario)}>
                          <Edit className="h-4 w-4" />
                        </Button>
                        <Button size="sm" variant="ghost" onClick={() => handleDeleteClick(usuario)}>
                          <Trash className="h-4 w-4" />
                        </Button>
                        {/* Adicionar botão de gerenciar permissões */}
                        <Button
                          size="sm"
                          variant="ghost"
                          onClick={() => router.push(`/usuarios/permissoes/${usuario.id}`)}
                          title="Gerenciar Permissões"
                        >
                          <Shield className="h-4 w-4" />
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>

      {/* Diálogo de confirmação de exclusão */}
      <Dialog open={!!usuarioParaExcluir} onOpenChange={(open) => !open && cancelarExclusao()}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Confirmar Exclusão</DialogTitle>
            <DialogDescription>
              Tem certeza que deseja excluir o usuário {usuarioParaExcluir?.nome}? Esta ação não pode ser desfeita.
            </DialogDescription>
          </DialogHeader>
          <DialogFooter>
            <Button variant="outline" onClick={cancelarExclusao} disabled={excluindo}>
              Cancelar
            </Button>
            <Button variant="destructive" onClick={excluirUsuario} disabled={excluindo}>
              {excluindo ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
              {excluindo ? "Excluindo..." : "Excluir"}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}
